.navbar{
    position: fixed;
    top:50px;
    left:10px;
    display:flex;
    flex-direction: column;
    background-color: white;
    padding:10px 20px;
    border-radius: 5px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.navbar a{
    text-decoration: none;
    color:rgb(49, 49, 49);
    font-size: 12px;
    text-align: center;
}
.navbar a:last-child{
    margin-top:20px;
}